.Sidebar_wrapper {
  display: flex;
  height: 100vh;
  width: 30%;
  min-width: 400px; }

.Sidebar_heading {
  background-color: #2C7AB9;
  height: 60px;
  display: flex;
  padding-left: 10px;
  align-items: center; }

.Sidebar_label {
  color: #FFFFFF;
  font-size: small;
  font-weight: normal;
  margin-top: -10px; }

.Sidebar_heading img {
  min-width: 20px;
  height: 30px;
  padding-left: 10px; }

.Sidebar_heading h3 {
  color: white;
  font-weight: bold; }

.Sidebar-menu {
  margin-bottom: 10px; }

.FeedbackLabel {
  color: #f8e05a; }

.Sidebar_menu {
  align-items: inherit;
  background-color: #2c7ab9;
  /*  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px; */
  border-right: 1px solid rgba(250, 250, 250, 0.43);
  /*box-shadow: -5px 0px 60px 0 rgba(0, 0, 0, 0.6), inset 0 0 40px rgba(0, 0, 0, 0.1);*/
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  margin-top: 30px;
  display: flex;
  margin: 0;
  width: 15%;
  min-width: 58px; }

.Sidebar_data {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  /*min-width: 350px;*/
  width: 85%;
  /*
  display: inherit;
  height: inherit;
  min-width: inherit;
  max-width: 350px;
  */
  /*box-shadow: -5px 0px 70px 0 rgba(0, 0, 0, 0.4);*/ }

.Sidebar_content {
  /*flex-grow: 1;*/
  flex: 1;
  overflow-y: auto; }

.md-content {
  /*overflow: inherit;*/ }

.organization_form_container {
  overflow: hidden; }

.Sidebar__nav {
  top: 0; }

.descriptionlabel {
  color: #03a9f4;
  font-size: 14px;
  margin-left: 10px; }

.Sidebar__nav-list {
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center; }

.Sidebar__nav-list__item {
  color: rgba(255, 255, 255, 0.86);
  cursor: pointer;
  font-size: 1.75rem;
  padding: 10px 15px;
  text-align: center; }

.Sidebar__nav-list__item:hover {
  color: white; }

.Sidebar__nav-list__item--active {
  color: white; }

.mycontainer {
  min-height: 600px; }

.md-button.md-fab {
  /*position:fixed;*/
  top: 0;
  margin: 0;
  font-size: x-large;
  color: #ffffff !important;
  background-color: #ff8a80 !important;
  margin-left: -10px; }

md-fab-speed-dial.md-down md-fab-trigger {
  margin-left: -23px; }

.md_button_submit {
  background: cornflowerblue;
  font-weight: bold;
  color: #FFFFFF; }

a.FeedbackLabel {
  color: #f8d96e !important; }

.feedback_button {
  position: absolute;
  bottom: 10px;
  left: 1px;
  color: #ff8a80;
  font-weight: bold; }
