@import url(https://fonts.googleapis.com/css?family=Montserrat);
ul {
  padding: 0;
  margin: 0;
  list-style-type: none; }

a {
  text-decoration: none; }

a:link, a:visited {
  /*color: #B0B0B2;*/
  color: #4b538a; }

a:hover, a:active {
  color: #0674D7;
  text-decoration: underline; }

p {
  font-size: 14px;
  margin-bottom: 0; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 300;
  margin: 0;
  padding: 0; }

.wrapper {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100vh;
  margin: 0 auto; }

/**
 * 1. z-indexed over the sidebar so the sidebar's shadow
 *    doesn't show over the main content area
 */
.Button {
  border-radius: 3px;
  border: 0;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 10px 20px 0 rgba(0, 0, 0, 0.1);
  color: white;
  cursor: pointer;
  display: inline-block;
  font-weight: 500;
  font-size: 13px;
  letter-spacing: 1px;
  outline: 0;
  padding: 16px 36px;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  -webkit-transition: box-shadow 0.15s, background-color 0.15s;
  transition: box-shadow 0.15s, background-color 0.15s;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  vertical-align: middle;
  will-change: box-shadow, background-color, color; }

.Button--sync {
  background-color: #00BAFF;
  border-radius: 50%;
  color: white;
  font-size: 32px;
  line-height: 70px;
  text-align: center;
  padding: 0;
  height: 70px;
  width: 70px; }

.Button--sync > span {
  line-height: 70px; }

#sync {
  bottom: 0;
  position: absolute;
  right: 0;
  -webkit-transform: translate(-50%, 50%);
  transform: translate(-50%, 50%); }

.u-flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }

.u-flex-center--v {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center; }

/*This is for search box*/
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@-webkit-keyframes color-1 {
  0% {
    background-color: #EB73B9; }
  100% {
    background-color: #17EAD9; } }

@keyframes color-1 {
  0% {
    background-color: #EB73B9; }
  100% {
    background-color: #17EAD9; } }

::-moz-selection {
  background: #6078EA;
  color: white;
  text-shadow: none; }

::selection {
  background: #6078EA;
  color: white;
  text-shadow: none; }

::-webkit-selection {
  background: #6078EA;
  color: white;
  text-shadow: none; }

.field-container {
  position: relative;
  padding: 0;
  margin: 0;
  border: 0;
  width: 480px;
  height: 40px; }

.icons-container {
  position: absolute;
  top: 11px;
  right: -25px;
  width: 35px;
  height: 35px;
  overflow: hidden; }

.icon-close {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 75%;
  height: 75%;
  opacity: 0;
  cursor: pointer;
  -webkit-transform: translateX(-200%);
  transform: translateX(-200%);
  border-radius: 50%;
  -webkit-transition: opacity 0.25s ease, -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
  transition: opacity 0.25s ease, -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
  transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
  transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1), -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1); }

.icon-close:before {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  border: 2px solid transparent;
  border-top-color: #6078EA;
  border-left-color: #6078EA;
  border-bottom-color: #6078EA;
  -webkit-transition: opacity 0.2s ease;
  transition: opacity 0.2s ease; }

.icon-close .x-up {
  position: relative;
  width: 100%;
  height: 50%; }

.icon-close .x-up:before {
  content: "";
  position: absolute;
  bottom: 2px;
  left: 3px;
  width: 50%;
  height: 2px;
  background-color: #6078EA;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg); }

.icon-close .x-up:after {
  content: "";
  position: absolute;
  bottom: 2px;
  right: 0px;
  width: 50%;
  height: 2px;
  background-color: #6078EA;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg); }

.icon-close .x-down {
  position: relative;
  width: 100%;
  height: 50%; }

.icon-close .x-down:before {
  content: "";
  position: absolute;
  top: 5px;
  left: 4px;
  width: 50%;
  height: 2px;
  background-color: #6078EA;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg); }

.icon-close .x-down:after {
  content: "";
  position: absolute;
  top: 5px;
  right: 1px;
  width: 50%;
  height: 2px;
  background-color: #6078EA;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg); }

.is-type .icon-close:before {
  opacity: 1;
  -webkit-animation: spin 0.85s infinite;
  animation: spin 0.85s infinite; }

.is-type .icon-close .x-up:before, .is-type .icon-close .x-up:after {
  -webkit-animation: color-1 0.85s infinite;
  animation: color-1 0.85s infinite; }

.is-type .icon-close .x-up:after {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

.is-type .icon-close .x-down:before, .is-type .icon-close .x-down:after {
  -webkit-animation: color-1 0.85s infinite;
  animation: color-1 0.85s infinite; }

.is-type .icon-close .x-down:before {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

.is-type .icon-close .x-down:after {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s; }

.icon-search {
  position: relative;
  top: 5px;
  left: 8px;
  width: 50%;
  height: 50%;
  opacity: 1;
  border-radius: 50%;
  border: 3px solid #c7d0f8;
  -webkit-transition: opacity 0.25s ease, -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
  transition: opacity 0.25s ease, -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
  transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
  transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1), -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1); }

.icon-search:after {
  content: "";
  position: absolute;
  bottom: -9px;
  right: -2px;
  width: 4px;
  border-radius: 3px;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  height: 10px;
  background-color: #c7d0f8; }

.field {
  border: 0;
  width: 100%;
  height: 100%;
  padding: 5px 20px;
  background: white;
  border-radius: 3px;
  box-shadow: 0px 8px 8px rgba(75, 72, 72, 0.1);
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease; }

.field:focus {
  outline: none;
  box-shadow: 0px 9px 9px rgba(75, 72, 72, 0.3); }

.field:focus + .icons-container .icon-close {
  opacity: 1;
  -webkit-transform: translateX(0);
  transform: translateX(0); }

.field:focus + .icons-container .icon-search {
  opacity: 0;
  -webkit-transform: translateX(200%);
  transform: translateX(200%); }
