.checkbox {
  background-color: #E6E6E6;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  text-align: center;
  vertical-align: middle;
  height: 20px;
  width: 20px; }

.checkbox:before {
  border-radius: 50%;
  background-color: #00BAFF;
  color: white;
  font-size: 32px;
  line-height: 28px;
  opacity: 0;
  -webkit-transition: all 0.1s ease-in;
  transition: all 0.1s ease-in;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }

.checkbox-input {
  display: none; }

.checkbox-input:checked + .checkbox:before {
  opacity: 1;
  position: absolute;
  top: -4px;
  right: -4px;
  bottom: -4px;
  left: -4px; }

