.box {
  height: 700px;
  background: url("/img/homebackground.png"); }

#app-shell-content {
  background: url("/img/homebackground.png"); }

.notext {
  height: 700px;
  width: 700px;
  border: 1px solid blue;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  display: inline-block;
  background-color: black;
  opacity: 0.5; }

.inner {
  position: relative;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  display: inline-block;
  color: white;
  font-size: xx-large;
  z-index: 10; }
