.Event-image {
  height: inherit;
  width: 100px;
  padding-right: 20px; }

.Events-title {
  color: black;
  padding: 5px 0 0 20px; }

.Event-section {
  /*background-color: #807F7F;*/
  width: 100%; }

.Event_data {
  margin: 0; }

.Event-description {
  /*width: 180px;*/
  overflow: hidden;
  text-overflow: ellipsis;
  color: #272727;
  margin: 5px 1px; }

.Event-card {
  /*background: #4b538a;*/
  height: 100%; }

.Card-icon {
  color: rgba(255, 255, 255, 0.3);
  cursor: pointer;
  padding: 10px 15px;
  font-size: 1.7rem; }

.Card-title {
  line-height: 24px;
  /*color:#FFFFFF;*/
  color: #515151;
  font-weight: bold; }

md-card-actions div p {
  color: #272727; }

/* End defaults*/
.card {
  display: inline-block;
  vertical-align: top;
  width: 94%;
  position: relative;
  overflow: hidden;
  margin: 10px;
  background: #FFF;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  color: #272727;
  border-radius: 2px; }

.card .fab {
  position: absolute;
  bottom: -20px;
  right: 10px;
  height: 50px;
  width: 50px;
  padding: 1rem;
  border-radius: 25px;
  background-color: #E91E63;
  color: white;
  border: 0;
  font-size: 1.4rem;
  line-height: 10px;
  outline: none; }
  .card .fab:active {
    animation: squish 0.75s ease; }

.card .title {
  font-size: 18px; }

.card .free_or_paid {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 10px; }

.card .content {
  padding: 20px;
  font-weight: 300;
  border-radius: 0 0 2px 2px; }

.card p {
  margin: 0; }

.card .action {
  border-top: 1px solid rgba(160, 160, 160, 0.2);
  padding: 20px; }

.card a {
  color: #1976D2;
  margin-right: 20px;
  transition: color 0.3s ease;
  text-transform: uppercase;
  text-decoration: none; }

.card .image {
  min-height: 100px;
  position: relative;
  /*background: #3949AB;*/
  background: #2c7ab9; }

.card .image img {
  border-radius: 2px 2px 0 0;
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%; }

.card .image .title {
  position: absolute;
  bottom: 0;
  left: 0;
  /*padding: 0 20px;*/
  padding: 5px 20px;
  color: #FFF;
  font-size: 14px; }

.card .image .free_or_paid {
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 0 20px;
  color: #FFF; }

.content .title {
  font-size: 18px;
  line-height: 24px;
  font-weight: bold;
  margin-bottom: 10px; }

span.Event_date {
  font-size: 12px;
  color: black;
  padding: 2px;
  font-weight: 400; }

md-divider.event_divider {
